<template>
  <div>
    <h2 class="mb-4">step {{currentStep}} of {{lastStep}}</h2>
    <v-form 
      :id="formConfig.id" 
      @submit.prevent="handleFormSubmit" 
      ref="form"
      :name="formConfig.identifier"
    >
      <field-renderer
          v-for="fieldData in formConfig.elements"
          :key="fieldData.identifier"
          :formName="formConfig.id"
          :fieldData="fieldData"
      />
      <div class="d-flex justify-space-between mt-4">
        <o-btn
          type="button"
          v-if="currentStep>1"
          @click="loadPreviousStep"
        >
          {{previousButtonLabel}}
        </o-btn>


        <o-btn
            type="submit"
            v-if="isLastStep"
            :loading="loading"
        >
          {{nextButtonLabel}}
        </o-btn>
        <o-btn
            type="submit"
            v-else
            :loading="loading"
        >
          {{nextButtonLabel}}
        </o-btn>
      </div>
    </v-form>
  </div>
 
</template>

<script>
  export default {
    computed: {
      formConfig(){
        return this.$store.getters.getCurrentSchema
      },
      loading(){
        return this.$store.state.loading
      },
      currentStep(){
        return this.$store.state.currentStep
      },

      lastStep(){
        return this.$store.state.lastStep
      },

      isLastStep(){
        return this.currentStep===this.lastStep
      },
      buttonLabels(){
        return (this.formConfig && 
                this.formConfig.api && 
                this.formConfig.api.page && 
                this.formConfig.api.page.labels)
      },
      previousButtonLabel(){
        return this.buttonLabels && this.buttonLabels.previousButtonLabel || "previous step"
      },

      nextButtonLabel(){
        return this.buttonLabels && this.buttonLabels.nextButtonLabel || "next step"
      }
    },
    methods: {
      generateFormFieldName(inputName){
        return`tx_form_formframework[${this.formConfig.id}][${inputName}]`
      },
      handleFormSubmit(){
        const form=this.$refs.form;
        if(form) this.$store.dispatch('submitStep', form);
        else console.log("could not find form element")
      },
      loadPreviousStep(){
        this.$store.commit('updateFormStep', this.currentStep - 1)
      }
    }
  }
</script>
